export default {
	data() {
		return {
			currentFilter: 0,
			currentSort: 0,
			isSort: false,
			isScreen: false,
			filterTop: false
		};
	},
	props: {
		filterData: {
			type: Object,
			default: () => {}
		}
	},
	computed: {
		edit() {
			let edit = false;
			if(this.filterData.screenBy){
				this.filterData.screenBy.forEach(screen => {
					screen.data.forEach(item => {
						if (item.select) {
							edit = true;
						}
					});
				});
				return edit;
			}
		}
	},
	components: {},
	methods: {
		filterSort(index) {
			this.currentFilter = index;
			switch (index) {
				case 0:
					this.filterTop = true;
					this.isSort = true;
					this.isScreen = false;
					break;
				case 1:
					this.$emit('update', {
						condition: this.filterData.navTab[1].condition
					});
					this.hideView();
					break;
				case 2:
					this.$emit('update', {
						condition: this.filterData.navTab[2].condition
					});
					this.hideView();
					break;
				case 3:
					this.filterTop = true;
					this.isScreen = true;
					this.isSort = false;
					break;
				default:
					this.hideView();
					break;
			}
		},
		selectSort(item, index) {
			this.currentSort = index;
			this.filterData.navTab[0].name = this.filterData.sortBy[index].name;
			this.hideView();

			// 更新数据
			this.$emit('update', { condition: item.code });
		},
		hideView() {
			// if(!this.filterBoolean) {
				this.filterTop = false;
			// }
			this.isSort = false;
			this.isScreen = false;
		},
		selectScreen(item, list) {
			if (list.id !== 'MPI') {
				// 单选
				list.data.forEach(ele => {
					ele.select = false;
				});
			}
			item.select = !item.select;
			console.log(item.select);
		},
		clearFilter() {
			this.filterData.screenBy.forEach(screen => {
				screen.data.forEach(item => {
					item.select = false;
				});
			});
		},
		filterOk() {
			let screenData = {
				MPI: '',
				offer: '',
				per: ''
			};
			let mpiStr = [];
			this.filterData.screenBy.forEach(screen => {
				screen.data.forEach((item, index) => {
					if (item.select) {
						// 两种情况 1.多选 2.单选
						if (screen.id !== 'MPI') {
							// 单选
							screenData[screen.id] = item.code;
						} else {
							// 多选 fengniao,pinpai
							mpiStr.push(item.code);
						}
					}
				});
				screenData[screen.id] = mpiStr.join();
			});
			// console.log(mpiStr.join());
			this.$emit('update', { condition: screenData });
			this.hideView();
		},
		moveHandle() {}
	}
};
